export const updateObject = (oldObject, newProperties) => {
    return {
        ...oldObject,
        ...newProperties
    }
}

export const checkValidity = (value, rules = {}) => {
    let isValid = true;

    if (rules.required) {
        isValid = value.trim() !== '' && isValid;
    }

    if (rules.minLength) {
        isValid = value.length >= rules.minLength && isValid;
    }

    if (rules.maxLength) {
        isValid = value.length <= rules.minLength && isValid;
    }

    if (rules.isEmail) {
        var pattern = /\S+@\S+\.\S+/;
        isValid = pattern.test(value) && isValid;
    }

    return isValid;
}